import { makeStyles } from "@material-ui/core";

// menu classes
const useStyles = makeStyles((theme) => ({
  main: {
    backgroundColor: "#FFFFFF",
    borderBottom: "1px solid #EFEFEF",
    display: "flex",
    height: "55px",
    position: "fixed",
    top: 0,
    left: 0,
    width: "100%",
    zIndex: 1000,
  },
  header: {
    height: "100%",
    width: "100%",
    margin: "0 auto",

    "& .sign-up": {
      alignItems: "center",
      backgroundColor: "#12B0EE",
      borderRadius: "8px",
      color: "#ffffff",
      cursor: "pointer",
      display: "flex",
      fontFamily: "Roboto",
      fontSize: "14px",
      fontWeight: 900,
      height: "39px",
      justifyContent: "center",
      lineHeight: "14px",
      margin: 0,
      minWidth: "88px",
      padding: 0,
      textTransform: "capitalize",
    },

    "& .sign-in": {
      alignItems: "center",
      backgroundColor: "#ffffff",
      border: "2px solid #12B0EE",
      borderRadius: "8px",
      color: "#12B0EE",
      cursor: "pointer",
      display: "flex",
      fontFamily: "Roboto",
      fontSize: "14px",
      fontWeight: 900,
      height: "39px",
      justifyContent: "center",
      lineHeight: "14px",
      marginLeft: "10px",
      minWidth: "88px",
      padding: 0,
      textTransform: "capitalize",
      transition: "all 0.2s ease",

      "&:hover": {
        backgroundColor: "#12B0EE",
        color: "#ffffff",
        border: "2px solid #12B0EE",
      },
    },
  },
  logo: {
    width: "152px",
    minWidth: "152px",
  },
  "nav-toggle": {
    alignItems: "flex-end",
    backgroundColor: "transparent",
    cursor: "pointer",
    display: "flex",
    flexDirection: "column",
    height: "32px",
    justifyContent: "space-evenly",
    margin: "0 0 0 auto",
    padding: 0,
    width: "32px",

    "& .line": {
      backgroundColor: "#24272C",
      borderRadius: "1px",
      display: "inline-block",
      height: "3px",
      opacity: 0.7,
      width: "20px",
    },

    "&::before, &::after": {
      backgroundColor: "#24272C",
      borderRadius: "1px",
      content: '""',
      display: "inline-block",
      height: "3px",
      opacity: 0.7,
    },
    "&::before": {
      width: "25px",
    },
    "&::after": {
      width: "15px",
    },

    "&:visited": {
      backgroundColor: "transparent",
    },
    "&:hover": {
      backgroundColor: "transparent",
    },
  },
  mobileMenu: {
    position: "fixed",
    width: "100vw",
    height: "100vh",
    padding: "18px 21px",
    right: 0,
    top: 0,
    zIndex: 2000,
    backgroundColor: "#FFFFFF",
    transform: "translateX(100%)",
    transition: "transform 0.2s ease-in-out",

    "&.open": {
      transform: "translateX(0)",
    },

    "& .mobile-nav-container": {
      display: "flex",
      flexDirection: "column",
      height: "100%",

      "& .action": {
        display: "flex",
        justifyContent: "end",
        marginBottom: "36px",

        "& .nav-close": {
          alignItems: "center",
          backgroundColor: "transparent",
          backgroundImage:
            'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20viewBox%3D%220%200%2020%2020%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cg%20opacity%3D%220.7%22%3E%3Crect%20width%3D%2225%22%20height%3D%223%22%20rx%3D%221%22%20transform%3D%22matrix(-0.707107%20-0.707107%20-0.707107%200.707107%2019.7991%2017.6777)%22%20fill%3D%22%2324272C%22%2F%3E%3Crect%20width%3D%2225%22%20height%3D%223%22%20rx%3D%221%22%20transform%3D%22matrix(-0.707107%200.707107%200.707107%200.707107%2017.6777%200)%22%20fill%3D%22%2324272C%22%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E%0A")',
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          cursor: "pointer",
          display: "flex",
          flexDirection: "column",
          height: "20px",
          justifyContent: "space-evenly",
          margin: 0,
          padding: 0,
          width: "20px",

          "&:visited": {
            backgroundColor: "transparent",
          },
          "&:hover": {
            backgroundColor: "transparent",
          },
        },
      },

      "& .logo": {
        "& img": {
          width: "unset",
          height: "52px",
        },
        marginBottom: "36px",
      },

      "& .mobile-nav": {
        "& > ul": {
          listStyle: "none",

          "& > li": {
            marginBottom: "12px",

            "& > a": {
              alignItems: "center",
              color: "#000000",
              cursor: "pointer",
              display: "flex",
              fontWeight: 900,
              fontSize: "36px",
              justifyContent: "space-between",
              lineHeight: "40px",
              opacity: 0.7,
              textDecoration: "none",

              "&:hover": {
                opacity: 1,
              },
            },

            "&.has-sub-menu > a": {
              "& strong": {
                marginRight: "10px",
              },

              "&::after": {
                content:
                  'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2224%22%20height%3D%2215%22%20viewBox%3D%220%200%2024%2015%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M2%202L12.4819%2012L22%202%22%20stroke%3D%22black%22%20stroke-width%3D%224%22%2F%3E%3C%2Fsvg%3E")',
                cursor: "pointer",
                maxHeight: "15px",
                lineHeight: 0,
              },

              "&.open": {
                "&::after": {
                  transform: "rotateX(180deg)",
                },
              },
            },
          },

          "& .sub-menu": {
            display: "none",
            maxHeight: "0px",
            overflow: "hidden",
            transition: "max-height 0.5s cubic-bezier(0, 1, 0, 1)",

            "&.open": {
              display: "block",
              maxHeight: "99em",
              transition: "max-height 0.5s ease-in-out",
            },

            "& ul": {
              padding: "20px 13px",
              listStyle: "none",

              "& li": {
                "&:not(:last-child)": {
                  marginBottom: "16px",
                },

                "& a": {
                  borderRadius: "10px",
                  color: "#24272C",
                  display: "flex",
                  flexDirection: "column",
                  fontFamily: "Roboto",
                  fontSize: "12px",
                  fontWeight: "normal",
                  lineHeight: "14px",
                  padding: "12px 14px",
                  textDecoration: "none",

                  "&:hover": {
                    background: "#E5F8FF",
                  },

                  "&>strong": {
                    fontSize: "18px",
                    fontWeight: 900,
                    marginBottom: "7px",
                  },

                  "&>.medium": {
                    fontWeight: 500,
                    marginBottom: "7px",
                  },

                  "&>.thin": {
                    maxWidth: "222px",
                    opacity: 0.7,
                  },
                },
              },
            },
          },
        },
      },

      "& .sign-up": {
        alignItems: "center",
        backgroundColor: "#12B0EE",
        borderRadius: "8px",
        color: "#ffffff",
        cursor: "pointer",
        display: "flex",
        fontFamily: "Roboto",
        fontSize: "14px",
        fontWeight: 900,
        height: "48px",
        justifyContent: "center",
        lineHeight: "14px",
        marginLeft: 0,
        marginRight: "14px",
        padding: 0,
        flexGrow: 1,
      },
      "& .sign-in": {
        alignItems: "center",
        backgroundColor: "#ffffff",
        border: "2px solid #12B0EE",
        borderRadius: "8px",
        color: "#12B0EE",
        cursor: "pointer",
        display: "flex",
        fontFamily: "Roboto",
        fontSize: "14px",
        fontWeight: 900,
        height: "48px",
        justifyContent: "center",
        lineHeight: "14px",
        margin: 0,
        padding: 0,
        flexGrow: 1,
      },
    },
  },
  [theme.breakpoints.up("xs")]: {
    header: {
      maxWidth: "90%",
    },
    menuIcon: {
      marginRight: "-15px",
    },
  },
  [theme.breakpoints.up("sm")]: {
    header: {
      width: "95%",
    },
    logo: {
      marginRight: "34px",
    },
  },
  [theme.breakpoints.up("md")]: {
    main: {
      height: "66px",
    },
    header: {
      width: "90%",
      maxWidth: "1100px",
    },
    nav: {
      alignItems: "center",
      display: "flex",
      height: "100%",
      justifyContent: "center",
      width: "100%",

      "& > ul": {
        alignItems: "center",
        backgroundColor: "#ffffff",
        display: "flex",
        height: "100%",
        justifyContent: "space-evenly",
        position: "relative",
        maxWidth: "100%",
        width: "445px",
        zIndex: 2,

        "& > li": {
          fontWeight: "bold",
          fontSize: "18px",
          lineHeight: "14px",
          listStyle: "none",

          "& a": {
            alignItems: "center",
            color: "#4F5665",
            cursor: "pointer",
            display: "inline-flex",
            textDecoration: "none",
          },

          "& a:visited": {
            color: "#4F5665",
            textDecoration: "none",
          },

          "& a:hover": {
            color: "#4F5665",
          },

          "&.has-sub-menu > a": {
            "& strong": {
              marginRight: "10px",
            },

            "&::after": {
              content:
                'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2210px%22%20height%3D%226px%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%20viewBox%3D%220%200%2010%206%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M8.83331%201L5.16665%205L1.49998%201%22%20stroke%3D%22%234F5665%22%20stroke-width%3D%221.4%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%2F%3E%3C%2Fsvg%3E")',
              cursor: "pointer",
              maxHeight: "6px",
              lineHeight: 0,
              transform: "rotateX(0deg)",
              transition: "transform 0.2s ease-in-out",
            },

            "&.open": {
              "&::after": {
                transform: "rotateX(180deg)",
              },
            },
          },

          "& ul": {
            "&.sub-menu": {
              background: "#FFFFFF",
              boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.25)",
              display: "flex",
              flexDirection: "column",
              marginTop: "1px",
              padding: "28px 16px",
              left: 0,
              listStyle: "none",
              position: "absolute",
              top: "100%",
              transform: "translateY(-300%)",
              transition: "transform 0.2s ease-in-out",
              width: "100%",
              overflow: "hidden",
              zIndex: 1,

              "&.open": {
                transform: "translateY(0)",
              },

              "&>li": {
                "&>a": {
                  alignItems: "center",
                  borderRadius: "10px",
                  color: "#24272C",
                  display: "flex",
                  fontFamily: "Roboto",
                  fontSize: "12px",
                  fontWeight: "normal",
                  justifyContent: "space-between",
                  padding: "12px 15px",
                  lineHeight: "14px",

                  "&:hover": {
                    background: "#E5F8FF",
                  },

                  "&>.left": {
                    display: "flex",
                    flexDirection: "column",

                    "&>strong": {
                      fontSize: "18px",
                      fontWeight: 900,
                      marginBottom: "7px",
                    },

                    "&>span": {
                      fontWeight: 500,
                    },
                  },

                  "&>.right": {
                    width: "222px",
                    opacity: 0.7,
                  },
                },
              },

              "&>li:not(:last-child)": {
                marginBottom: "24px",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    header: {
      maxWidth: "80%",
    },
  },
  [theme.breakpoints.up("xl")]: {
    header: {
      width: "70%",
    },
  },
}));

export default useStyles;
